@import '../../../../base';

.checkout-v2-container {
  .checkout-header-container {
    .insurer-container {
      display: flex;
      align-items: center;

      @media screen and (max-width: 575px) {
        h2 {
          font-size: $font-size-small;
        }
      }
    }

    .insurer-logo {
      max-width: 100px;
      max-height: 100px;
      margin-right: 10px;

      img {
        width: 100%;
        height: auto;
      }
    }
  }
}