@import '../../../../base';

.checkout-v2-container {
  .checkout-desc-item-container {
    .header-container {
      display: flex;
      margin-bottom: 30px;

      .tooltip-container {
        margin-left: 20px;
      }
    }

    .main-desc-container {
      .icon-container {
        padding-right: 32px;
        @media screen and (max-width: 575px) {
          margin-bottom: 10px;
        }

        .icon-img-container {
          width: 100%;
          height: 100%;
          max-width: 80px;
          max-height: 80px;

          img {
            width: 100%;
            height: auto;
          }
        }
      }
      
      .icon-desc {
        font-size: $font-size-smallest;
        // margin-bottom: 50px;
      }
  
      .desc-container {
        margin-top: 13px;

        &.desc-child {
          margin-top: 0;
          p {
            margin: 0;
          }

          @media screen and (max-width: 575px) {
            .dark-text {
              text-align: right;
            }
          }
        }

        .grey-text {
          color: $color-input-form-landing-desc2;
          font-weight: $font-weight-normal;
        }

        .dark-text {
          @media screen and (min-width: 576px) {
            text-align: right;
          }
        }
      }
    }
    
    .collapse-container {
      padding: 10px 0 20px 0;
    }

    .wordings-container {
      margin-bottom: 20px;
      a {
        margin-left: 12px;
        font-weight: $font-weight-bold;
        font-size: $font-size-smallest;
        color: $color-input-form-landing-desc;
      }
    }
  }
}
