@import '../../../../base';

.checkout-v2-payment-modal {
  max-width: 458px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;

  .modal-content {
    padding-left: 20px;
    padding-right: 20px;

    .method-container {
      width: 100%;
      display: flex;
      align-items: center;
      margin-bottom: 30px;

      .method-button {
        width: 143px;
        height: 48px;
        border-radius: 50px;
        transition: background-color 0.3s ease;
        
        color: $color-input-text;
        font-weight: $font-weight-bold;
        font-size: $font-size-tiny;
  
        &.selected {
          background-color: $color-input-light-blue;
        }
  
        &:nth-of-type(1) {
          margin-right: 10px;
        }
      }
    }
    
    .pay-deposit-container {
      margin-top: 10px;
    }

    .price-container {
      margin-top: 10px;
      font-weight: $font-weight-bold;
      font-size: $font-size-smallest;
      color: $color-input-text;

      span:nth-of-type(2) {
        float: right;
      }
    }

    .button-container {
      display: flex;
      width: 100%;

      .cc-button,
      .bank-button {
        button {
          height: 48px;
  
          p {
            font-size: 14px;
          }
  
          min-width: 100% !important;
          @media screen and (max-width: 576px) {
            padding: 0 !important;
          }
        }
      }

      .error-container {
        font-weight: $font-weight-bold;
        color: $color-error-alt;
        font-size: $font-size-small;
      }
    }

    .cc-button {
      width: 100%;
      padding-right: 5px;

      @media screen and (max-width: 576px) {
        margin-bottom: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-right: 0;
      }

      button {
        border: 1px solid $color-input-form-landing-desc2;
      }

      p {
        color: $color-input-form-landing-desc2;
        font-weight: $font-weight-normal;
        font-size: $font-size-tiny;
        margin: 0;
      }

      &:hover {
        button {
          border: 1px solid $color-hover;

          p {
            color: $color-hover;
          }
        }
      }
    }

    .bank-button {
      padding-left: 5px;
      width: 100%;

      @media screen and (max-width: 576px) {
        display: flex;
        justify-content: center;
        padding-left: 0;
      }
    }
  }

  .popup {
    &:before {
      background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
      opacity: 0.9;
    }

    .checkout-button {
      min-width: 168px !important;
      margin: auto;
    }
  }
}