@import '../../../base';

.business-bank-transfer-container {
  .inner-container {
    max-width: 600px;
    margin: 0 auto 20px auto;
    padding: 30px 40px;
    background: white;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
    border-radius: 20px;

    .title {
      font-size: $font-size-regular;
      font-weight: $font-weight-bold;
      color: $color-input-text;
      text-align: center;
      margin-bottom: 20px;
      padding: 0 40px;
    }
    .normal-text {
      font-size: $font-size-tiny;
      font-weight: $font-weight-semibold;
      color: $color-input-text;
      margin-bottom: 0px;
    }
    .bold-text {
      font-size: $font-size-smallest;
      font-weight: $font-weight-bold;
      color: $color-input-text;
      margin-bottom: 0px;
    }
    .right {
      text-align: right;
    }
    h5.footnote {
      font-size: $font-size-tiny;
      font-weight: $font-weight-normal;
      color: $color-other-desc;
      padding-left: 10px;
    }

    .pps-details {
      margin: auto;
      width: 350px;

      .pps-name {
        font-size: $font-size-regular;
        font-weight: $font-weight-bold;
        color: $color-hover;
        text-align: center;
        margin-bottom: 10px;
      }

      .bank-details {
        display: flex;
        padding-left: 30px;
        align-items: center;

        .bank-img {
          width: 100%;

          img {
            width: 100%;
            height: auto;
          }
        }
      }
    }

    .cta-btn {
      margin: 40px 0 10px;
      
      .button-container {
        display: flex;
        max-width: 324px;
        margin: auto;
  
        button {
          height: 48px;
  
          p {
            font-size: 14px;
          }
  
          min-width: 100% !important;
          @media screen and (max-width: 576px) {
            padding: 0 !important;
          }
        }
      }

      .later-button {
        width: 100%;
        padding-right: 5px;
  
        @media screen and (max-width: 576px) {
          margin-bottom: 10px;
        }
  
        button {
          border: 1px solid $color-input-form-landing-desc2;
        }
  
        p {
          color: $color-input-form-landing-desc2;
          font-weight: $font-weight-normal;
          font-size: $font-size-tiny;
        }
  
        &:hover {
          button {
            border: 1px solid $color-hover;
  
            p {
              color: $color-hover;
            }
          }
        }
      }
  
      .submit-button {
        padding-left: 5px;
        width: 100%;
      }
    }

    @media screen and (max-width: 767px) {
      text-align: center;

      .info-group {
        margin-bottom: 10px;
      }
      .right {
        text-align: center;
      }
      h5.footnote {
        text-align: left;
      }
      .title {
        padding: 0px;
      }
      .bank-details {
        flex-direction: column;
        justify-content: center;
        padding-left: 0px !important;

        .bank-img {
          width: 70% !important;
          margin-left: auto;
          margin-right: auto;
        }
      }
    }

    .foot-note {
      text-align: center;
      font-weight: $font-weight-normal;
      font-size: $font-size-tiny;

      color: $color-input-form-landing-desc2;
    }
  }
}
