@import '../../../../../base';

.checkout-v2-container {
  .more-button {
    img {
      margin-left: 12px;
      width: 10px;
      height: 7px;

      filter: $color-filter-input-form-landing-desc2;
      transition: transform 0.3s ease;
    }

    .rotate-arrow {
      transform: rotate(180deg);
    }
  }

  .checkout-collapse-more-container {
    margin-top: 0;

    .more-collapse-container {
      border: 1px solid $color-input-outline;
      box-sizing: border-box;
      border-radius: 20px;
      padding: 20px;
      margin-bottom: 10px;

      .col-4:nth-of-type(2) {
        .grey-text{
          justify-content: center;
        }
      }

      .col-4:nth-of-type(3) {
        .grey-text{
          float: right;
        }
      }
    }
  }
}
