@import '../../../../base';

.checkout-v2-container {
  .checkout-price-summary-container {
    h2,
    .dark-text,
    .grey-text {
      display: flex;
      align-items: baseline;
      span:nth-of-type(1) {
        flex: 1;
        padding-right: 10px;
      }
    }

    .grey-text {
      color: $color-input-form-landing-desc2;
      font-weight: $font-weight-normal;
      
    }

    .sub_text {
      transform: translateY(-5px);
      color: $color-other-desc;
    }

    .main {
      margin-bottom: 34px;
    }

    .discount-label {
      margin-bottom: 18px;
      @media screen and (min-width: 768px) {
        margin-bottom: 0;
      }
    }

    .discount-label-without-promo {
      @media screen and (min-width: 768px) {
        position: absolute;
      }
    }

    .price-item {
      display: flex;
      align-items: center;
      height: 100%;

      .dark-text {
        flex: 1;

        span:nth-of-type(1) {
          margin-right: 10px;
        }
        
        span:nth-of-type(2) {
          word-wrap: break-word;
          text-align: right;
        }
      }
    }

    .total-container {
      padding: 10px 0;
    }

    .big-value {
      font-size: $font-size-big;
    }

    .button-container {
      margin-top: 40px;

      button {
        float: right;
      }
    }

    .left-price {
      padding-right: 10px;
    }

    .input-wrapper-main-v3  {
      padding: 0;
    }

    .promo-code-container {
      padding: 20px 0;

      .promo-code {
        display: flex;
        align-items: flex-end;
        .input-wrapper-main-v3  {
          padding-right: 10px;
        }
      }
    }

    hr {
      margin: 20px 0;
    } 
  }
}