@import '../../../../base';

.checkout-v2-container {
  .checkout-form-container {
    .green-text {
      margin-bottom: 20px;
    }
    .dark-text {
      float: right;
      font-size: 14px;

      @media screen and (max-width: 767px) {
        float: left;
      }
    }

    .field-container {
      width: 100%;
      @media screen and (max-width: 767px) {
        margin-bottom: 10px;
      }

      .name-container {
        display: flex;
        align-items: center;
  
        p {
          font-weight: $font-weight-normal;
          font-size: $font-size-smallest;
  
          color: $color-input-text;
        }
      }
  
      .field {
        .input-wrapper-main-v3  {
          margin-top: 0;
          padding: 0;
        }
      }
    }
  }
}