@import '../../../../base';

.business-checkout-banner {
  border: none !important;
  background-color: transparent !important;
  padding: 0 !important;
  margin-bottom: 10px;

  @media screen and(max-width: 991px) {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  .business-checkout-banner-inner {
    display: flex;
    align-items: center;
    background: $color-input-form-landing-desc;
    padding-left: 20px;
    padding-right: 20px;
    height: 89px;

    h2 {
      font-size: 21px;
      font-weight: $font-weight-heavy;

      color: white;
      flex: 1;

      margin: 0;
    }

    .proceed-image-container {
      display: flex;
      height: 100%;
      align-items: flex-end;
    }
  }
}