@import '../../../base';

.checkout-v2-container {
  margin-top: 30px;
  margin-bottom: 50px;

  @media screen and (max-width: 991px) {
    margin-top: 0;
  }

  h2 {
    font-size: $font-size-big;
    font-weight: $font-weight-bold;
    color: $color-input-text;
  }

  hr {
    background: $color-input-outline;
    border-radius: 20px;
    margin: 30px 0;
  }

  .grey-text {
    font-weight: $font-weight-semibold;
    font-size: $font-size-tiny;
    color: $color-other-desc;
    white-space: pre-wrap;
  }

  .dark-text {
    font-weight: $font-weight-bold;
    font-size: $font-size-tiny;
    color: $color-input-text;
    white-space: pre-wrap;
  }

  .green-text {
    font-weight: $font-weight-bold;
    font-size: $font-size-regular;
    color: $color-hover;
    white-space: pre-wrap;
  }

  .left-container {
    padding: 10px 40px 30px 40px;
    margin-bottom: 20px;
    margin-right: 40px;

    @media screen and (max-width: 991px) {
      margin-right: 0;
    }
  }

  .right-container {
    padding: 30px;
    margin-left: 0;

    @media screen and (max-width: 991px) {
      margin-left: 0;
    }
  }

  .left-container {
    border-radius: 20px;
    border: 1px solid  $color-input-outline;
  }

  .right-container {
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
  }

  .left-container,
  .right-container {
    background: white;
    box-sizing: border-box;

    @media screen and (max-width: 991px) {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  hr {
    margin-top: 10px;
  }

  .popup {
    &:before {
      background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
      opacity: 0.9;
    }

    .checkout-button {
      min-width: 168px !important;
      margin: auto;
    }
  }
}

.pay-deposit-container {
  background: $product-background;
  border-radius: 20px;
  padding: 5px;

  p {
    font-weight: $font-weight-semibold;
    font-size: $font-size-tiny;
    text-align: center;
    margin: 0;
    color: $color-input-form-landing-desc2;
  }
}
